import { Content } from "./content";

export class List {
  constructor(listEl, list) {
    this.list = list;
    this.listEl = listEl;
    this.contentEl = document.querySelector("#content");
    this.content = new Content(this.contentEl); // ÑÐ¾Ð·Ð´Ð°ÐµÐ¼ ÑÐ·ÐµÐ¼Ð¿Ð»ÑÑ€ ÐºÐ»Ð°ÑÑÐ° Content (content.js)
    this.init();
  }

  init() {
    // Ð·Ð°Ð¿ÑƒÑÐºÐ°ÐµÐ¼ Ð¼ÐµÑ‚Ð¾Ð´ render
    this.render();
  }

  render() {
    this.listEl.innerHTML = "";
    // Ð¿Ñ€Ð¾Ñ…Ð¾Ð´Ð¸Ð¼ÑÑ Ð¿Ð¾ list (data.json) Ð¸ Ð²ÑÑ‚Ð°Ð²Ð»ÑÐµÐ¼ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ñ title Ð¸ id Ð² ÑÐ¾Ð·Ð´Ð°Ð²Ð°ÐµÐ¼Ñ‹Ðµ li
    this.list.forEach((item, index) => {
      const liEl = document.createElement("li");
      liEl.classList = "main-section__item";
      liEl.setAttribute("data-id", item.id);
      liEl.innerHTML = `${item.title}<time class="main-section__time">10.01.2021 14:00</time>`;
      // Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð²Ñ‹Ð·Ð°Ð²Ð°ÐµÐ¼ Ð¼ÐµÑ‚Ð¾Ð´ render(item) - Ð¾Ñ‚Ñ€Ð¸ÑÐ¾Ð²Ñ‹Ð²Ð°ÐµÑ‚ÑÑ content ÑÐ¾Ð¾Ñ‚Ð²ÐµÑ‚ÑÑ‚Ð²ÑƒÑŽÑ‰Ð¸Ð¹ li
      liEl.addEventListener("click", (event) => {
        // Ð·Ð°Ð¿Ð¸ÑÑ‹Ð²Ð°ÐµÐ¼ Ð² Ð¼Ð°ÑÑÐ¸Ð² Ð²ÑÐµ li (Ð´Ð¾ ÑÑ‚Ð¾Ð³Ð¾ Ð±Ñ‹Ð» Ð¿ÑÐµÐ²Ð´Ð¾Ð¼Ð°ÑÑÐ¸Ð² - html ÐºÐ¾Ð»ÐµÐºÑ†Ð¸Ñ)
        const items = Array.from(
          document.querySelectorAll(".main-section__item")
          );
          // ÑƒÐ´Ð°Ð»ÑÐµÐ¼ ÐºÐ»Ð°ÑÑ active
          items.forEach((elem) => {
            elem.classList.remove("main-section__item--active");
          });
          // Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ ÐºÐ»Ð°ÑÑ active
          liEl.classList.add("main-section__item--active");
          this.content.render(item);
        });
        if (index === 0 && this.list.length > 0) {
          liEl.classList.add("main-section__item--active");
          this.content.render(item);
        }
      this.listEl.append(liEl); // Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·ÑƒÐµÐ¼ append Ð´Ð»Ñ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ñ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° ÑÐ¾Ð·Ð´Ð°Ð½Ð½Ð¾Ð³Ð¾ Ð´Ð¸Ð½Ð°Ð¼Ð¸Ñ‡ÐµÑÐºÐ¸
    });
  }
}
